import {StatusBar} from 'expo-status-bar';
import React, {useEffect} from 'react';
import {StyleSheet, Text, View} from 'react-native';

import color from '../colors';
import ProgressBar from '../components/progress_bar';
import ContentContainer from '../components/content_container';
import AppButton from '../components/button';
import Bottom_pop from "../components/bottom_pop";
import {lesson} from "../services/storage/database";
import QuizScreen from "./quize";

import {ContentType} from '../services/storage/model'
import {addInfo, incrementIndex} from '../store/sublesson'
import {useAppSelector, useAppDispatch} from '../app/hook'


export const onePageLesson = lesson.subLessons![0];


export default function ContentScreen() {
  const {isSelected} = useAppSelector((state) => state.question)
  const {index, infos} = useAppSelector(state => state.subLesson);
  const dispatch = useAppDispatch();
  function onPress() {
    if (index >= onePageLesson.contents.length - 1){
      return;
    }
    dispatch(incrementIndex());
  }

  useEffect(() => {
    return () => {
      if (onePageLesson.contents[index].type === ContentType.info) {
        const newInfo = onePageLesson.contents[index].content
          dispatch(addInfo(newInfo));
      }
    };
  }, [index]);


  function checkIsInfo(){
    return onePageLesson.contents[index].type === ContentType.info
  }
  function renderQuestion() {
    let content = onePageLesson.contents[index];


    if (content.type === ContentType.question){
      const questionData = content.content;

      return (
          <>
            <View style={styles.content}>
              <QuizScreen question={questionData}/>
            </View>

            {isSelected && <Bottom_pop question={content.content} /> }
          </>
      )
    }
  }

  function  renderInfos() {
    return (
        <>
          <View style={styles.content}>
          {
            infos.map((info, index) => (
                    <ContentContainer key={index} content={info.text} />
                )
            )
          }
          </View>

          <View style={styles.button}>
            <AppButton content='Continue' onPress={onPress} />
          </View>
        </>
    )
  }


  return (
    <View style={styles.container}>
      <ProgressBar />
      <View style={styles.heading}>
        <Text style={styles.heading_text}>{onePageLesson.title}</Text>
      </View>


      { checkIsInfo() ? renderInfos(): renderQuestion() }



      <StatusBar style="auto" />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: color.surfaceContainerLowest,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'space-between',
    maxWidth: 500,
    width: '100%',
    marginHorizontal: 'auto',
    paddingHorizontal: 5,
  },

  button: {
  },
  content: {
    width: '100%',
    alignSelf: 'flex-start',
    flex: 1,
  },

  heading: {

  },

  heading_text: {
    color: color.onSurface,
    fontSize: 25,
    fontWeight: 'bold',
  },
})